@import "breakpoints";

.container{
}

.content {
  margin: 0 auto;
  max-width: $xl;
  padding: 0 20px;
  @media (max-width: $xl) {
    padding: 0 50px;
  }
  @media (max-width: $md) {
    padding: 0 38px;
  }
  @media (max-width: $sm) {
    padding: 0 20px;
  }
}

.block_separate {
  margin-top: 100px;
  @media (max-width: $lg) {
    margin-top: 40px;
  }
}

body, html {
  font-family: "Geometria", sans-serif;
}